@import "./../../colors.scss";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.panel-container {
  padding: 20px;
  height: 100vh;
  background: $background;
  box-sizing: border-box;
  .prompt {
    color: $white;
    height: 22px;
    padding-bottom: 5px;
  }
  .messages-container {
    background: $secondary;
    border-radius: 10px;
  }
  .form-container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  .top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  .mailbox-cont {
    display: flex;
    justify-content: space-between;
  }
  h1 {
    color: $white;
    margin-bottom: 10px;
    span {
      color: $blue;
    }
  }
  .email {
    display: flex;
    color: #7b0dbf;
    align-items: center;
    font-size: 22px;
    margin-bottom: 10px;
    img {
      margin-right: 7px;
    }
  }
  .mess-button {
    transition: 0.2s;
    color: $background;
    background: $white;
    border: none;
    border-radius: 5px;
    padding: 2px;
    font-size: 20px;
    width: 185px;
    height: 36.5px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    img {
      width: 25px;
      height: 25px;
      margin-right: 5px;
    }
  }
  .mess-button:hover {
    background: $secondaryHover;
    color: $white;
  }
  input,
  button {
    font-size: 19px;
  }
  .error {
    color: red;
    border: 1px solid red;
  }
  .comunicator {
    text-align: center;
    min-height: 70px;
    max-height: 70px;
    width: 265px;
    display: flex;
    font-size: 20px;
    align-items: center;
    transition: 0.2s;
    border: 3px solid transparent;
    padding: 0px;
    border-radius: 6px;
    font-weight: 600;
    img {
      height: 60px;
      width: 65px;
    }
  }
  .comunicator-false {
    opacity: 1;
    color: red;
    border: 3px solid red;
  }
  .comunicator-true {
    opacity: 1;
    color: #fff;
    border: 3px solid rgb(23, 189, 23);
  }
  input {
    border: none;
    border: 1px solid $white;
    background: transparent;
    padding: 10px;
    border-bottom-left-radius: 8px;
    color: $white;
  }
  .logout {
    height: 35px;
    top: 10px;
    right: 10px;
    border: none;
    border-radius: 8px;
    color: $white;
    font-size: 20px;
    cursor: pointer;
    width: 200px;
    padding: 5px;
    color: $background;
    &:hover {
      background: $secondaryHover;
      color: $white;
      border: 0.1px solid $white;
    }
  }
  .messages,
  .new-post {
    width: 600px;
  }
  .new-post {
    margin-bottom: 40px;
    display: grid;
    color: $white;
    grid-gap: 15px;
    height: 110px;
    grid-template-columns: 1fr 1fr;
    .important {
      background: linear-gradient(245.3deg, #071136 25.83%, #092692 93.39%);
      border-radius: 5px;
      padding: 10px;
      input {
        height: 20px;
        text-align: left;
        width: 20px;
      }
      p {
        margin: 0;
        padding-bottom: 10px;
      }
    }
    button {
      background: $secondary;
      color: $white;
      border: none;
      border-radius: 5px;
      padding: 10px;
      font-size: 20px;
      &:hover {
        background: $secondaryHover;
      }
    }
  }
  .messages {
    p {
      max-width: 285px;
      margin-bottom: 5px;
      span {
        margin-right: 5px;
      }
    }
    height: 400px;
    border-radius: 10px;
    background: $blue;
    overflow: scroll;
  }
  h2 {
    color: $white;
    padding: 10px;
  }
  .message {
    color: $white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px 10px;
    font-size: 18px;
    border-bottom: 2px solid $white;
    span {
      color: $background;
      font-weight: 700;
    }
    div {
      display: flex;
      justify-content: space-between;
      margin-bottom: 7px;
    }
  }
  .content {
    display: flex;
    justify-content: space-between;
    textarea {
      border-radius: 5px;
      padding: 3px;
      border: none;
      width: 180px;
      height: 36.5px;
    }
  }
  .read {
    padding: 2px;
    background: $secondary !important;
    border: none;
    border-radius: 5px;
    color: $white;
  }
  .received-header {
    padding-right: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    img {
      width: 35px;
      height: 35px;
    }
    .refreshed {
      transform: rotate(360deg);
      transition: 0.5s;
      animation-timing-function: ease-in-out;
    }
  }
}
